AA,CACA,kBAAA,CAEA,8CACE,gBAAA,CAEF,8CACE,eAAA,CAAA,CAGJ,0BACE,8CACE,gBAAA,CAEF,8CACE,eAAA,CAAA,CAMJ,yBADF,6BAEI,WAAA,CAAA,CAKN,mBACE,eAAA,CAEA,yBAHF,mBAII,YAAA,CAAA,CC3LJ,yBADF,qBAEI,SAAA,CACA,aAAA,CAAA,CAEF,yBALF,qBAMI,SAAA,CACA,aAAA,CAAA,CAEJ,2BACI,aXRG,CWSH,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,mBAAA,CACA,eAAA,CACA,kBAAA,CAGF,8BACE,aXnBG,CWoBH,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,qBAAA,CAEA,qCACE,UAAA,CACA,iBAAA,CACA,wDAAA,CACA,iCAAA,CACA,2BAAA,CACA,qBAAA,CACA,UAAA,CACA,WAAA,CCnCF,gBACI,aAAA,CACA,gBAAA,CACA,gBAAA,CACA,2DAAA,CACA,4BAAA,CACA,yBANJ,gBAOM,gBAAA,CAAA,CAGN,qBACI,aZTM,CYUN,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,wBAAA,CAEF,8BACE,YAAA,CACA,OAAA,CACA,gBAAA,CACA,eAAA,CACA,WAAA,CACA,wBZzBD,CY0BC,yBAPF,8BAQI,YAAA,CAAA,CAGL,mCACG,aZ7BD,CY8BC,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,qBAAA,CAEF,yBADD,8DAEK,YAAA,CAAA,CAEL,0BAJA,8DAKC,YAAA,CAAA,CAKN,iBACI,UAAA,CAEA,yBAHJ,iBAIM,YAAA,CACA,eAAA,CACA,0BAAA,CAAA,CAIJ,yBACE,cAAA,CACA,qBAAA,CAEA,yBAJF,yBAKI,cAAA,CACA,aAAA,CAAA,CAGF,0BATF,yBAUI,UAAA,CACA,YAAA,CACA,cAAA,CAAA,CAKJ,uBACE,aAAA,CAGF,4BACE,eAAA,CAEA,yBAHF,4BAII,YAAA,CAAA,CAIJ,mCACE,wBZpFU,CYqFV,WAAA,CACA,UAAA,CCzFJ,OACI,aAAA,CAGJ,gBACI,SAAA,CACA,kBAAA,CACA,eAAA,CACA,gBAAA,CAEA,yBANJ,gBAOK,gBAAA,CACC,YAAA,CACA,6BAAA,CACA,QAAA,CAAA,CAEF,0BAZJ,gBAaQ,gBAAA,CACA,mBAAA,CACA,gBAAA,CACA,YAAA,CACA,uCAAA,CACA,aAAA,CAAA,QAAA,CAAA,CAGJ,2BACI,eAAA,CACA,abxBM,CayBN,iBAAA,CACA,cAAA,CACA,iBAAA,CACA,eAAA,CACA,gBAAA,CACA,yBACM,wCACE,eAAA,CACA,WAAA,CAEA,wCACA,gBAAA,CACA,WAAA,CAAA","sourcesContent":["// /* ШРИФТ */\r\n\r\n@font-face {\r\n    font-family: \"TT Lakes\";\r\n    src: local(\"TT Lakes Regular\"), url(\"/src/fonts/TTLakes-Regular.woff\") format(\"woff\"), url(\"/src/fonts/TTLakes-Regular.eot\") format(\"embedded-opentype\");\r\n    font-weight: 400;\r\n    font-style: normal;\r\n    font-display: swap;\r\n  }\r\n  \r\n  @font-face {\r\n    font-family: \"TT Lakes\";\r\n    src: local(\"TT Lakes Medium\"), url(\"/src/fonts/TTLakes-Medium.woff\") format(\"woff\"), url(\"/src/fonts/TTLakes-Medium.eot\") format(\"embedded-opentype\");\r\n    font-weight: 500;\r\n    font-style: normal;\r\n    font-display: swap;\r\n  }\r\n  \r\n  @font-face {\r\n    font-family: \"TT Lakes\";\r\n    src: local(\"TT Lakes Bold\"), url(\"/src/fonts/TTLakes-Bold.woff\") format(\"woff\"), url(\"/src/fonts/TTLakes-Bold.eot\") format(\"embedded-opentype\");\r\n    font-weight: 700;\r\n    font-style: normal;\r\n    font-display: swap;\r\n  }","*,\r\n::before,\r\n::after {\r\n    padding: 0;\r\n    margin: 0;\r\n    border: 0;\r\n    font-family:\"TT Lakes\";\r\n    font-size: 14px;\r\n    list-style-type: none;\r\n    text-decoration: none;\r\n}\r\n\r\nbody{\r\n    background-color: $back;\r\n    color: $text;\r\n}\r\n.body{\r\n    @media(min-width: 1440px){\r\n        display: flex; \r\n    }\r\n}\r\n\r\n.body-container {\r\n    max-width: 1920px;\r\n}\r\nbutton{\r\n    cursor: pointer;\r\n    background-color: transparent;\r\n    outline: none;\r\n    &:hover,\r\n    :focus{\r\n     opacity: 0.6;\r\n     transition: 0.3s ease-in-out;\r\n}\r\n}","$back: #F8F8F8;\r\n$back-menu:#FFF;\r\n$text: #1B1C21;\r\n$text-focuse: #7e7e82;\r\n$border-focuse: #B8FFEC;\r\n$border-list: 1px solid #d9fff5;\r\n$line: #EAEAEA;\r\n$element: rgba(65, 246, 215, 1);\r\n$button:  #FF3E79;\r\n$color-black:rgba(224, 224, 224, 0);\r\n\r\n",".background-white {\r\n    position: fixed;\r\n    width: 100%;\r\n    height: 100%;\r\n    background-color: $back-menu;\r\n    opacity: 0;\r\n    z-index: -1;\r\n  \r\n    &--active {\r\n      opacity: 0.9;\r\n      z-index: 2;\r\n      transition: opacity 0.6s ease-in-out, z-index 0.3s ease-in-out;\r\n    }\r\n  }","\r\n.mobile-menu {\r\n    position: fixed;\r\n    width: 320px; \r\n    background-color: $back-menu;\r\n    transition: left 500ms ease-in-out;\r\n    z-index: 2;\r\n    overflow-y: auto;\r\n    left: -150%;\r\n\r\n    &--open {\r\n        height: 100%;\r\n        left:0;\r\n    }\r\n    @media (min-width: 360px) {\r\n        width: 360px;\r\n      }\r\n    \r\n    @media (min-width: 768px) {\r\n        width: 320px;\r\n      }\r\n    \r\n    @media (min-width: 1440px) {\r\n        display: block;\r\n        position: static;\r\n        box-shadow: none;\r\n        z-index: 1;\r\n        min-width: 320px;\r\n      }\r\n\r\n    &__header-aside{\r\n        width: 100%;\r\n        margin-top: 18px;\r\n        display: flex;\r\n        justify-content: space-between;\r\n        align-items: center;\r\n\r\n        @media(min-width: 1440px){\r\n            margin-bottom: 37px;\r\n        }\r\n        @media(max-width: 768px){\r\n            margin-bottom: 0px;\r\n        }\r\n    }\r\n\r\n    &__header-aside-part-left{\r\n        display: flex;\r\n        flex-direction: row;\r\n        gap:24px;\r\n        align-items: center;\r\n        margin-left: 24px;\r\n        @media(max-width: 360px){\r\n            gap:24px;\r\n        }\r\n    }\r\n    &__header-aside-part-right{\r\n        display: flex;\r\n        flex-direction: row;\r\n        align-items: center;\r\n        margin-right: 24px;\r\n    }\r\n    &__navigation-aside{\r\n        padding: 0px 0px;\r\n        margin-bottom: 24px;\r\n    }\r\n    &__footer-aside{\r\n        margin-left: 24px;\r\n        @media(min-width: 1120px){\r\n            margin-top: 268px;\r\n        }\r\n        @media (max-width: 768px) {\r\n            margin-top: 16px;\r\n        }\r\n    }\r\n}\r\n\r\n.navigation-aside {\r\n\r\n    &__list {\r\n        display: flex;\r\n        flex-direction: column;\r\n        justify-content: space-between;\r\n        gap: 16px;\r\n    }\r\n\r\n    &__list-item {\r\n        &:focus,\r\n        &:hover {\r\n        content: \"\";\r\n        width: 4px;\r\n        height: 32px;\r\n        background-color: $element;\r\n        border-radius: 0px 10px 10px 0px;\r\n        white-space: nowrap; \r\n        transition: 0.3s ease-in-out;\r\n        }\r\n    }\r\n\r\n    &__link {\r\n        color:$text;\r\n        font-size: 16px;\r\n        font-style: normal;\r\n        font-weight: 500;\r\n        line-height: 32px; \r\n        padding: 0 0 0 24px;\r\n        &:focus,\r\n        &:hover {\r\n          color: $text-focuse;\r\n          transition: 0.3s ease-in-out;\r\n        }\r\n\r\n    }\r\n}\r\n\r\n.footer-aside{\r\n    \r\n    &__contacts {\r\n        display: flex;\r\n        justify-content: flex-start;\r\n        gap: 16px;\r\n        padding-bottom: 16px;\r\n        @media (max-width: 768px) {\r\n            padding-bottom: 8px;\r\n        }\r\n    }\r\n\r\n    &__lang-options {\r\n        display: flex;\r\n        justify-content: flex-start;\r\n        gap: 7px;\r\n        padding: 0;\r\n        @media (max-width: 1440px) {\r\n        padding-top: 40px;\r\n        padding-bottom: 32px;\r\n        }\r\n        @media (min-width: 768px) {\r\n            padding-top: 16px;\r\n            padding-bottom: 16px;\r\n        }\r\n}\r\n}\r\n\r\n.link{\r\n    color: $text;\r\n    font-size: 16px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: 24px; \r\n\r\n    &__email{\r\n        display: block;\r\n        padding-bottom: 12px;\r\n        &:hover,\r\n        &:focus{\r\n            color:$text-focuse;\r\n            transition: 0.3s ease-in-out;\r\n            cursor: pointer; \r\n        }\r\n    }\r\n    &__phone{\r\n        display: block;\r\n        font-size: 24px;\r\n        font-style: normal;\r\n        font-weight: 700;\r\n        line-height: 32px;\r\n        letter-spacing: -0.6px;\r\n        &:hover,\r\n        &:focus{\r\n            color: $text-focuse;\r\n            transition: 0.3s ease-in-out;\r\n            cursor: pointer; \r\n        }\r\n    }\r\n}\r\n.button-aside--open {\r\n    height: 100%;\r\n    left: 0;\r\n  }\r\n\r\n.lang-link{\r\n  &_active{\r\n    color: $text-focuse;\r\n} \r\n    &:hover,\r\n    &:focus{\r\n        display: block;\r\n        color:  $text-focuse;\r\n        cursor: pointer;\r\n        transition: 0.3s e